.movie-details_box {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  padding: 15px;
  border-radius: 5px;
}

.movie-player {
  flex: 2;
}

.movie-details {
  color: white;
  flex: 3;
}

.movie-details h2 {
  font-size: 1.7rem;
  padding-bottom: 20px;
  border-bottom: solid 4px white;
  margin-bottom: 20px;
}

.movie-details p:last-child {
  margin-top: 20px;
}

/* MEDIA QUERIES */
@media (max-width: 768px) {
  .movie-details_box {
    flex-direction: column;
    align-items: center;
  }
  .movie-player {
    width: 100%;
  }
}
